body {
  color: #333;
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.audio-player-container {
  background-color: #fff;
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 20px;
  box-shadow: 0 4px 6px #0000001a;
}

.audio-player-container h3 {
  color: #2c3e50;
  margin-top: 0;
  margin-bottom: 15px;
}

audio {
  width: 100%;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  text-align: center;
  color: #2c3e50;
  margin-bottom: 60px;
}

.announcement-types {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-bottom: 40px;
  display: grid;
}

.announcement-btn {
  cursor: pointer;
  text-shadow: 1px 1px 1px #0000001a;
  background: linear-gradient(145deg, #ffffff1a 0%, #0000001a 100%);
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  min-height: 80px;
  padding: 40px 20px;
  font-size: 1.1em;
  font-weight: bold;
  transition: all .3s;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 6px #0000001a;
}

.announcement-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 8px #00000026;
}

.announcement-btn.selected {
  transform: scale(.95);
  box-shadow: 0 2px 4px #0000001a;
}

.checkmark {
  opacity: 0;
  font-size: 18px;
  font-weight: bold;
  transition: opacity .3s;
  position: absolute;
  top: 5px;
  right: 5px;
}

.announcement-btn.selected .checkmark {
  opacity: 1;
}

.controls {
  justify-content: space-between;
  gap: 20px;
  margin-bottom: 20px;
  display: flex;
}

.select-group {
  flex-direction: column;
  flex-grow: 1;
  display: flex;
}

label {
  margin-bottom: 5px;
  font-weight: bold;
}

select {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  padding: 16px;
  font-size: 16px;
}

.generate-btn-container {
  margin-bottom: 30px;
}

#generateBtn {
  color: #fff;
  cursor: pointer;
  background-color: #3498db;
  border: none;
  border-radius: 5px;
  width: 100%;
  padding: 15px;
  font-size: 18px;
  transition: background-color .3s;
}

#generateBtn:hover {
  background-color: #2980b9;
}

.feedback {
  background-color: #fff;
  border-radius: 5px;
  padding: 20px;
  display: none;
  box-shadow: 0 4px 6px #0000001a;
}

.feedback h3 {
  color: #2c3e50;
  margin-top: 0;
}

.feedback p {
  margin: 10px 0;
}

@media (width <= 600px) {
  .announcement-types {
    grid-template-columns: repeat(2, 1fr);
  }

  .controls {
    flex-direction: column;
  }
}

.custom-message {
  flex-direction: column;
  margin-bottom: 20px;
  display: flex;
}

.my-2 {
  margin: .5rem 0;
}

textarea {
  resize: vertical;
  border: 1px solid #ccc;
  border-radius: 5px;
  min-height: 100px;
  padding: 10px;
  font-family: Roboto, sans-serif;
}

.login-form {
  background-color: #fff;
  border-radius: 8px;
  max-width: 400px;
  margin: 100px auto;
  padding: 20px;
  box-shadow: 0 2px 4px #0000001a;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  margin-bottom: 5px;
  display: block;
}

.form-group input {
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  padding: 8px;
}

.error-message {
  color: red;
  text-align: center;
  margin-top: 10px;
}

.logout-btn {
  color: #fff;
  cursor: pointer;
  background-color: #f44;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  position: absolute;
  top: 20px;
  right: 20px;
}

.logout-btn:hover {
  background-color: #c00;
}
